html {
  width: 100%;
  box-sizing: border-box;
  fot-size: 62.5%;
  background-color: #1d1d1d;
}
.App {
  text-align: center;
}
header {
  background-color: #3bd665;
  width: 100%;
}
.header_title {
  font-size: 4rem;
  font-family: bungee, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #1d1d1d;
  height: 100px;
  padding-top: 12px;
  min-width: 750px;
  white-space: nowrap;
}

/* ------------ HottestMusic ---------------- */

.HottestMusic {
  background-color: #272727;
  width: 50%;
  height: 600px;
  max-width: 1400px;
  min-width: 750px;
  margin: 50px auto 70px;
  padding-top: 15px;
  border-radius: 30px;
  box-shadow: 8px 10px 30px;
}
.HottestMusic_title {
  color: #fff;
  font-family: bungee, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 3rem;
  margin-top: 15px;
  padding-bottom: 8px;
  border-bottom: solid 5px #3bd665;
}
.flex_wrapper {
  width: 100%;
  display: flex;
  justify-content: space-around;
  padding-top: 60px;
}
.HottestMusic_Album {
  width: 45%;
  object-fit: cover;
  border-radius: 16px;
  box-shadow: 5px 5px 15px #1d1d1d;
}
.HottestMusic_text {
  width: 45%;
  color: #fff;
}
.HottestMusic_text h1 {
  font-family: roc-grotesk, sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 4rem;
}
.Border {
  background-color: #fff;
  height: 3px;
  margin-top: -10px;
  margin-bottom: 10px;
}
.HottestMusic_text h2 {
  font-family: roc-grotesk, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 2rem;
}

/* ----------------------------------- */

/* ------------ Collection ----------- */
#global-nav {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 999;
}
.Collection_header {
  font-family: bungee, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #1d1d1d;
  font-size: 3rem;
  margin-top: 60px;
  background-color: #3bd665;
  height: 70px;
  padding-top: 5px;
}
.Border_2 {
  background-color: #3bd665;
  height: 5px;
  margin-top: 2px;
  margin-bottom: 5px;
}
.Collection_width {
  width: 80%;
  max-width: 1400px;
  min-width: 750px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

/* -----------card.js------------- */
.Card_layout {
  width: 25%;
  min-width: 300px;
  background-color: #272727;
  border-radius: 30px;
  box-shadow: 8px 10px 30px;
  position: relative;
  padding: 30px;
  margin-right: 60px;
  margin-top: 50px;
}
.Card_AlbumCover {
  width: 90%;
  border-radius: 10px;
}
.Card_text {
  color: #fff;
  font-family: roc-grotesk, sans-serif;
  font-weight: 100;
  font-style: normal;
  font-size: 2rem;
  margin-top: 10px;
}
.Card_text p {
  padding-top: 5px;
}
.Border_3 {
  background-color: #fff;
  height: 2px;
}
.List_title {
  color: #fff;
  font-family: roc-grotesk, sans-serif;
  font-weight: 100;
  font-style: normal;
  font-size: 1.5rem;
  margin-top: 20px;
}
.List {
  text-align: left;
  color: #fff;
  background-color: #1d1d1d;
  width: 100%;
  max-height: 150px;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  border-radius: 8px;
  margin-bottom: 10px;
}
.List::-webkit-scrollbar {
  display: none;
}
.List_text {
  margin-bottom: 5px;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 5px;
}
.List_text:last-child {
  margin-bottom: 0;
}
.flex {
  display: flex;
  justify-content: space-between;
}
.date {
  padding-top: 15px;
  padding-left: 2px;
  color: #fff;
}
